.users {
  padding: 20px 0;
  &__inner {
    background: #fff;
    border-radius: 20px;
    padding: 24px;
  }
  &__title {
    color: #003764;
    margin-bottom: 32px;
  }
  table {
    width: 100%;
    border-collapse: collapse;
    thead {
      tr {
        th {
          text-align: start;
          font-size: 14px;
          line-height: 150%;
          font-weight: 500;
          color: #0081DF;
          padding: 10px 16px;
        }
      }
    }
    tbody {
      tr {
        border-bottom: 1px solid #DCF1FF;
        td {
          text-align: start;
          font-size: 14px;
          line-height: 150%;
          font-weight: 500;
          color: #003764;
          padding: 10px 16px;
          &:last-child {
            width: 172px;
          }
        }
      }
    }
    .actions {
      display: flex;
      align-items: center;
      gap: 8px;
    }
    .table__edit {
      height: 33px;
      background: #B2E5FF;
      border-radius: 8px;
      padding: 4px 16px;
      &:hover {
        background: #6DD2FF;
      }
      span {
        font-size: 14px;
        line-height: 150%;
        color: #003764;
      }
    }
    .table__delete {
      height: 33px;
      background: #FFB2B2;
      border-radius: 8px;
      padding: 4px 16px;
      &:hover {
        background: #FF8888;
      }
      span {
        font-size: 14px;
        line-height: 150%;
        color: #640000;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .users {
    &__table {
      overflow-x: auto;
      &::-webkit-scrollbar {
        height: 4px;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #EEF8FF;
        border-radius: 3px;
      }
      table {
        width: 914px;
      }
    }
  }
}