.header {
  background: #fff;
  padding: 32px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__logo {
    width: 190px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__group {
    display: flex;
    gap: 8px;
  }
  &__btn {
    height: 40px;
    border-radius: 8px;
    padding: 4px 18px;
    span {
      font-size: 14px;
    }
  }
  &__btn2 {
    height: 40px;
    border-radius: 8px;
    padding: 4px 18px;
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 24px 0;
    &__logo {
      width: 160px;
    }
    &__btn,
    &__btn2 {
      gap: 8px !important;
      padding: 4px 12px !important;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__btn,
    &__btn2 {
      width: 32px;
      height: 32px;
      span {
        display: none;
      }
    }
  }
}