.wrapper {
  overflow: hidden;
}
h6 {
  font-size: 24px;
  line-height: 130%;
  font-weight: 500;
}
.btn-blue {
  gap: 12px;
  background: #003764;
  color: #fff;
  &:hover {
    background: #0081DF;
  }
  span {
    line-height: 150%;
  }
  i {
    max-width: 14px;
    flex-shrink: 0;
  }
  i svg {
    max-width: 100%;
    width: auto;
    height: auto;
    path {
      transition: all 0.3s;
    }
  }
}
.btn-gray {
  gap: 18px;
  background: #F5F5F6;
  color: #585962;
  &:hover {
    background: #fff;
    color: #242428;
    i svg path {
      stroke: #242428;
    }
  }
  span {
    line-height: 150%;
  }
  i {
    max-width: 14px;
    flex-shrink: 0;
  }
  i svg {
    max-width: 100%;
    width: auto;
    height: auto;
    path {
      transition: all 0.3s;
    }
  }
}
.date {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  &__title {
    font-size: 14px;
    line-height: 150%;
    font-weight: 500;
    color: #003764;
  }
  &__input {
    width: 140px;
    height: 42px;
    background-image: url("../img/date.svg");
    background-repeat: no-repeat;
    background-position: center right 16px;
    background-size: auto;
    background-color: #EEF8FF;
    border-color: transparent;
    border-radius: 8px;
    padding: 0 16px;
    font-size: 14px;
    line-height: 150%;
    color: #003764;
  }
  input::-webkit-calendar-picker-indicator {
    opacity: 0;
  }
}
.form__file {
  .file-wrap {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    label {
      width: 100%;
      height: 42px;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      border: 1px dashed #6DD2FF;
      border-radius: 8px;
      font-size: 14px;
      line-height: 150%;
      color: #003764;
    }
  }
  .file-block {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  .name {
    order: -1;
    font-size: 14px;
    line-height: 150%;
    color: #003764;
  }
  .file-delete {
    transform: rotate(-45deg);
    font-size: 24px;
    color: #003764;
    cursor: pointer;
  }
}
.modal {
  background: rgba(#000, .5);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    max-width: 816px;
    width: 100%;
    position: relative;
    background: #ffffff;
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, .15);
    border-radius: 20px;
    margin: 0 auto;
    padding: 16px;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
    @media screen and (max-width: 576px) {
      max-width: calc(100% - 20px);
    }
  }
  &__close {
    background: transparent;
    padding: 0;
    width: 12px;
    svg {
      width: 100%;
      height: auto;
    }
  }

}
.filters {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__title {
    color: #003764;
  }
  @media screen and (min-width: 993px) {
    .map__items {
      position: relative;
      width: 50%;
    }
    .map__item {
      &-hidden {
        left: 100%;
        border: 2px solid #EEF8FF;
        box-shadow: unset;
        padding: 24px;
        &:after {
          display: none;
        }
      }
      &-top {
        display: none;
      }
      &-checkboxes {
        height: 100%;
        margin-top: 0;
      }
    }
  }
  @media screen and (min-width: 577px) {
    .map__btns {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      gap: 32px;
      margin-top: 40px;
    }
    .map__btn {
      width: auto;
      padding: 4px 62px;
    }
    .map__reset {
      width: auto;
      padding: 0;
    }
  }
  .map__group {
    gap: 24px;
    margin-top: 40px;
  }
  .date {
    width: 50%;
  }
  .form__file {
    width: 50%;
  }
}

@media screen and (max-width: 992px) {
  h6 {
    font-size: 18px;
  }
}

@media screen and (max-width: 768px) {
  .filters {
    .map__group {
      flex-direction: column;
    }
    .date {
      width: 100%;
    }
    .form__file {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .filters {
    .date {
      flex-direction: column;
      align-items: start;
      &__title {
        br {
          display: none;
        }
      }
      &__input {
        width: 100%;
      }
    }
    .map__reset {
      order: 1;
    }
  }
}