.map {
  position: relative;
  padding: 20px 0 200px;
  &__inner {
    position: relative;
    z-index: 3;
    max-width: 400px;
    background: #fff;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    padding: 16px 24px;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__logo {
    display: block;
    width: 190px;
    img {
      width: 100%;
    }
  }
  &__icon {
    width: 20px;
    flex-shrink: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 32px;
  }
  &__filters {
    height: 42px;
    gap: 18px;
    background: #EEF8FF;
    border-radius: 8px;
    padding: 4px 16px;
    color: #003764;
    margin-top: 16px;
    span {
      font-size: 14px;
      line-height: 150%;
    }
    i {
      width: 14px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 32px;
  }
  &__btn {
    width: 100%;
    height: 47px;
    border-radius: 16px;
    padding: 4px 16px;
    span {
      font-size: 14px;
    }
  }
  &__reset {
    width: 100%;
    background: transparent;
    color: #0081DF;
    &:hover {
      color: #003764;
    }
    span {
      font-size: 14px;
      line-height: 150%;
      font-weight: 500;
    }
  }
  &__btns2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    margin-top: 32px;
  }
  &__btn2 {
    height: 32px;
    flex-grow: 1;
    border-radius: 8px;
    span {
      font-size: 12px;
    }
  }
  &__rights {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    line-height: 150%;
    color: #585962;
    margin-top: 16px;
  }
  &-iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &__google {
      width: 100%;
      height: 100%;
    }
    iframe {
      width: 100%;
      height: 100%;
      border: none;
    }
    &__item {
      position: absolute;
      z-index: 2;
      right: 30px;
      bottom: 30px;
    }
    &__item-show {
      background: #fff;
      box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.15);
      border-radius: 8px;
      padding: 16px 32px;
      color: #003764;
      cursor: pointer;
    }
    .active-show {
      display: none;
    }
    &__item-hidden {
      position: relative;
      width: 616px;
      display: none;
      background: #fff;
      box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.15);
      border-radius: 20px;
      padding: 24px 24px 24px 50px;
    }
    .active-hidden {
      display: block;
    }
    &__item-close {
      position: absolute;
      top: 24px;
      left: 24px;
      width: 12px;
      cursor: pointer;
      svg {
        width: 100%;
        height: auto;
      }
    }
    .date {
      width: 55%;
      &__title {
        font-size: 12px;
      }
    }
    .form__file {
      width: 45%;
    }
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
}
.map__item {
  &-show {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    border-radius: 8px;
    padding: 14px 16px;
    cursor: pointer;
    transition: all 0.3s;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -24px;
      width: 24px;
      height: 100%;
      background: transparent;
    }
    @media screen and (min-width: 993px) {
      &:hover {
        background: #EEF8FF;
        .map__item-btn {
          display: none;
        }
        .map__item-number {
          display: flex;
        }
      }
    }
  }
  &-title {
    font-size: 14px;
    line-height: 150%;
    font-weight: 500;
    color: #003764;
  }
  &-btn {
    width: 8px;
    flex-shrink: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &-number {
    width: 20px;
    height: 20px;
    display: none;
    justify-content: center;
    align-items: center;
    background: #003764;
    border-radius: 50%;
    span {
      font-size: 11px;
      line-height: 105%;
      color: #fff;
    }
  }
  &-hidden {
    position: absolute;
    top: 0;
    left: calc(100% + 6px);
    width: 100%;
    height: 100%;
    background: #fff;
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, .15);
    border-radius: 20px;
    padding: 16px 24px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.125s;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: -6px;
      width: 6px;
      height: 100%;
      background: transparent;
    }
  }
  .item-hidden--active {
    opacity: 1;
    pointer-events: auto;
  }
  &-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    border-bottom: 1px solid #EEF8FF;
    padding-bottom: 16px;
  }
  &-subtitle {
    font-size: 20px;
    line-height: 150%;
    font-weight: 500;
    color: #003764;
  }
  &-close {
    width: 12px;
    flex-shrink: 0;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &-checkboxes {
    height: 513px;
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 26px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: transparent;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #EEF8FF;
      border-radius: 3px;
    }
  }
  &-checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
        background-color: #003764;
      }
    }
    label {
      position: relative;
      display: block;
      font-size: 14px;
      line-height: 150%;
      font-weight: 500;
      color: #003764;
      padding-left: 30px;
      &:after {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 20px;
        height: 20px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        background-color: #DCF1FF;
        border-radius: 4px;
        transition: all 0.3s;
      }
    }
  }
}
.item--active {
  .item-show {
    background: #EEF8FF;
    .map__item-btn {
      display: none;
    }
    .map__item-number {
      display: flex;
    }
  }
}

@media screen and (max-width: 1600px) {
  .map {
    padding: 20px 0 60px;
  }
}

@media screen and (max-width: 992px) {
  .map {
    padding: 20px 0;
    &__inner {
      max-width: 100%;
    }
    &-iframe {
      position: relative;
      max-width: calc(100% - 30px);
      min-height: 300px;
      top: unset;
      left: unset;
      margin: 20px auto 0;
      &__google {
        position: relative;
        padding-bottom: 50%;
        border-radius: 20px;
      }
      iframe {
        position: absolute;
        top: 0;
        left: 0;
      }
      &__item {
        position: static;
        right: 16px;
        bottom: 16px;
      }
      &__item-show {
        display: inline-flex;
      }
      &__item-hidden {
        width: 100%;
      }
    }
  }
  .map__item {
    &-hidden {
      position: static;
      display: none;
      background: transparent;
      border-radius: 0;
      box-shadow: unset;
      padding: 14px 0 14px 16px;
      opacity: 1;
    }
    &-top {
      display: none;
    }
    &-checkboxes {
      height: auto;
      margin-top: 0;
      overflow: unset;
    }
  }
}

@media screen and (max-width: 768px) {
  .map {
    &-iframe {
      .map__group {
        flex-direction: column;
      }
      .date {
        width: 100%;
      }
      .form__file {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .map {
    &-iframe {
      &__item-show {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
      }
      .date {
        flex-direction: column;
        align-items: start;
        &__title {
          br {
            display: none;
          }
        }
        &__input {
          width: 100%;
        }
      }
      .map__reset {
        order: 1;
      }
    }
  }
}